















import Vue from "vue";
import Component from "vue-class-component";
import Carousel from "@/components/carousell.vue";
import { Movel } from "@/api";

@Component({
  components: {
    Carousel,
  },
})
export default class extends Vue {
  private movel: Movel;

  constructor() {
    super();
    const moveis = Movel.getMoveis();
    const index = parseInt(this.$route.params.id);
    const movel = moveis[index];
    this.movel = movel;
  }

  get wppLink(): string {
    const now = new Date().getHours();
    let comeco = "Boa noite";
    if (now >= 5 && now < 12) {
      comeco = "Bom dia";
    } else if (now >= 12 && now < 18) {
      comeco = "Boa tarde";
    }
    return (
      "https://wa.me/5511995401715/?text=" +
      encodeURI(
        `${comeco}, gostaria de mais informaes sobre a(o) ${this.movel.nome}`
      )
    );
  }

  get imagesMovel(): Array<unknown> {
    return this.movel.imgs.map((x) =>
      require("@/assets/moveis-db/imgmoveis/" + x)
    );
  }
}
