import Moveis from "@/assets/moveis-db/moveis.json";

export class Movel {
  nome: string;
  imgs: string[];
  tituloCatalogo: string;
  descricao: string;

  private constructor(
    nome: string,
    imgs: string[],
    tituloCatalogo: string,
    descricao: string
  ) {
    this.nome = nome;
    this.imgs = imgs;
    this.tituloCatalogo = tituloCatalogo;
    this.descricao = descricao;
  }

  static getMoveis(): Movel[] {
    const temp = Moveis.moveis;
    const moveisResult = temp.map(
      (x) => new Movel(x.nome, x.imgs, x.tituloCatalogo, x.descricao)
    );

    return moveisResult;
  }
}
